@mixin centrFlex($flex: flex, $justC: center, $alI: center) {
  display: $flex;
  justify-content: $justC;
  align-items: $alI;
}

@mixin formHero ($colorF: var(--accent-color), $borderF: none, $outlineF: 1px solid var(--accent-color), $paddingF: 7px) {
  color: $colorF;
  border: $borderF;
  outline: $outlineF;
  padding: $paddingF;
}

// $colorF: var(--accent-color);
// $borderF: none;
// $outlineF: 1px solid var(--accent-color);
// $paddingF: 10px;


%__link {
  font-weight: 700;
  color: var(--white-color);

  transition: color 250ms linear;
  &:hover,
  &:focus {
    color: var(--accent-color);
  }
}
.btn {
  @include centrFlex();
  font-size: 8px;
  line-height: 1.25;
  border-radius: 20px;
  @media screen and (min-width: $desktop) {
    font-size: 14px;
    line-height: 1.28;
  }
}
.page-header {
  overflow: hidden;
  position: relative;

  background-color: var(--primary-background-color);
}

.wrapper-nav {
  @media screen and (max-width: $pre-tablet) {
    padding-top: 6px;
  }
  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 27px;
    margin-bottom: 15px;
  }
  @media screen and (min-width: $desktop) {
    padding-top: 38px;
    margin-bottom: 84px;
    justify-content: end;
  }
}
.logo {
  @media screen and (max-width: $pre-tablet) {
    margin-top: 6px;
    margin-bottom: 137px;
  }
  @media screen and (min-width: $desktop) {
    position: relative;
    right: 110px;
  }
}
.nav-header {
  display: none;
  @media screen and (min-width: $desktop) {
    display: block;
    // margin-right: 110px;
  }
}
.nav {
  position: relative;
  z-index: 6;
  display: flex;
}
.nav__item:not(:last-child) {
  margin-right: 35px;
}
.nav__link {
  @extend %__link;
  letter-spacing: normal;
  font-size: 16px;
  line-height: 1.3;
}
.btn-buy--hero {
  display: none;
  @media screen and (min-width: $tablet) {
    z-index: 5;
    display: flex;

    width: 160px;
    height: 44px;

    color: var(--accent-color);
    background-color: var(--white-color);
    font-weight: 700;
    font-size: 13px;
    transition: background-color 250ms linear;
    // margin-bottom: 14px;
  }
  @media screen and (min-width: $desktop) {
    margin-left: 110px;
  }
  &:hover,
  &:focus{
    background-color: rgba(236, 235, 235, 1);
  }
}
.icon-btn-buy {
  // margin-left: 10px;
  width: 20px;
  height: 20px;
  
  transform: rotate(90deg);
  fill: var(--accent-color);
  stroke: var(--accent-color);
  stroke-width: 1px;
}
// TITLE H1----
.wrapper-title {
  @media screen and (max-width: $pre-tablet) {
    margin-bottom: 19px;
    position: relative;
    z-index: 2;
    width: 160px;
  }
  @media screen and (min-width: $tablet) {
    margin-bottom: 20px;
    width: 165px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 25px;
    width: 279px;
  }
}
.hero .hero-title {
  font-weight: 400;
  text-align: left;
  margin-bottom: 0;
  overflow-wrap: break-word;

  color: var(--white-color);
  @media screen and (min-width: $tablet) {
    font-size: 22px;
    line-height: 1.05;
    letter-spacing: 0.03em;
  }
  @media screen and (min-width: $desktop) {
    font-size: 38px;
    line-height: 1.15;
    letter-spacing: 0.04em;
  }
}

// .br {
//   display: block;
// }

.hero .section-title {
  @media screen and (min-width: $tablet) {
    font-size: 34px;
    line-height: 0.8;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: $desktop) {
    font-size: 58px;
    line-height: 0.9;
    letter-spacing: 0.04em;
  }
}
.title-btn {
  display: flex;
  margin-bottom: 160px;
  @media screen and (max-width: $mobile) {
    position: relative;
    z-index: 2;
  }
  @media screen and (min-width: $tablet) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 56px;
  }
}
.title-btn__item:not(:last-child) {
  @media screen and (max-width: $pre-desktop) {
    margin-right: 8px;
  }
  margin-right: 15px;
}
//---- BTN-----
.title-btn--red {
  height: 24px;
  width: 63px;
  color: var(--white-color);
  background-color: var(--accent-color);

  transition: filter 250ms linear;

  &:hover,
  &:focus {
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
  @media screen and (min-width: $desktop) {
    height: 40px;
    width: 104px;
  }
}
.title-btn--white {
  height: 24px;
  width: 82px;
  color: var(--accent-color);
  background-color: var(--white-color);
  transition: background-color 250ms linear;
  &:hover,
  &:focus {
    background-color: rgba(236, 235, 235, 1);
  }
  @media screen and (min-width: $desktop) {
    height: 40px;
    width: 138px;
  }
}
// ICE__CREAM
.icecream {
  bottom: 0;
  position: absolute;
  z-index: 1;
  @media screen and (max-width: $pre-tablet) {
    right: 32px;
  }
  @media screen and (min-width: $tablet) {
    right: calc(50% - 139px);
  }
  @media screen and (min-width: $desktop) {
    right: calc(50% - 192px);
  }
}
.icecream::after {
  position: absolute;
  top: -9px;
  left: -52%;
  z-index: -1;
  display: block;
  content: '';

  background-color: rgba(255, 165, 186, 1);
  border-radius: 50%;
  @media screen and (max-width: $pre-tablet) {
    top: -9px;
    left: -52%;
    min-width: 346px;
    height: 344px;
  }
  @media screen and (min-width: $tablet) {
    top: -20px;
    left: -51%;

    width: 324px;
    height: 322px;
  }
  @media screen and (min-width: $desktop) {
    top: -75px;
    left: -51%;

    width: 538px;
    height: 538px;
  }
}
// INFO
