    margin-bottom: 14px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 18px;
  }
  
}

.info__title {
  z-index: 6;
  position: relative;
  overflow: hidden;

  font-family: var(--secondary-font);
