  z-index: 6;
  bottom: 0;
  right: calc(50% - 349px);
  @media screen and (max-width: $pre-tablet) {
    display: none;
  }
  @media screen and (min-width: $desktop) {
    right: calc(50% - 531px);
  }
}
// MOBILE__MENU
.menu {
  position: absolute;
  z-index: 7;
  top: 0;
  right: 0;
  min-width: 250px;
  height: 100vh;
  padding: 60px 20px 0 20px;

  background-color: rgba(225, 121, 146, 1);
  filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));

  transform: translateX(0);
  transition: transform var(--transition-duration) var(--transition-function);

  @media screen and (min-width: $tablet) {
    min-width: 254px;
  }
  @media screen and (min-width: $desktop) {
    display: none;
  }
}
.menu.is-hidden {
  transform: translateX(120%);
  display: block;
}

.menu .btn-menu--close {
  display: block;
}
.menu.is-hidden .btn-menu {
  display: block;
}
.btn-menu {
  z-index: 5;
  width: 30px;
  height: 30px;
  position: absolute;
  background-color: transparent;
  @media screen and (max-width: $pre-tablet) {
    top: 16px;
    right: 15px;
  }
  @media screen and (min-width: $tablet) {
    // top: 45px;
    top: 37px;
    right: calc(50% - 169px);
  }
  @media screen and (min-width: $desktop) {
    display: none;
  }
}
.btn-menu--span {
  display: block;
  width: 20px;
  height: 0px;
  border: 1px solid var(--accent-color);
  &::after {
    position: absolute;
    bottom: 6px;
    left: 0;
    content: '';
    display: block;
    width: 20px;
    height: 0px;
    border: 1px solid var(--accent-color);
  }
  &::before {
    position: absolute;
    top: 6px;
    left: 0;
    content: '';
    display: block;
    width: 20px;
    height: 0px;
    border: 1px solid var(--accent-color);
  }
}
.icon-btn-menu {
  width: 20px;
  height: 12px;
}
.btn-menu--close {
  position: absolute;
  top: 24px;
  right: 28px;
  line-height: 0;
  background-color: transparent;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.icon-btn-menu--close {
  width: 14px;
  height: 14px;
  fill: var(--white-color);
}
.btn-buy {
  width: 209px;
  height: 44px;

  font-weight: 700;
  font-size: 13px;
  margin-bottom: 19px;

  color: var(--accent-color);
  background-color: var(--white-color);
  transition: background-color 250ms linear;
  @media screen and(min-width:768px) {
    width: 186px;
    margin-left: auto;
    margin-right: auto;
  }

  &:hover,
  &:focus{
    background-color: rgba(236, 235, 235, 1);
  }
}
.nav-menu__link {
  @extend %__link;
  padding: 13px 0;

  font-size: 14px;
  line-height: 1.4;
  border-top: 1px solid #e18298;
  &:last-child {
    border-bottom: 1px solid #e18298;
  }
}

